<template>
  <div class="main-board">
    <div class="weather-board">
      <div class="local">
        <img src="" alt="">
        <div class="district">{{district}}</div>
      </div>
      <div class="nowtmp">{{nowtmp}}
        <div class="degree-symbol">°</div>
      </div>
      <div class="weather-big-icon">
        <img :src="imgurl" alt="" />
      </div>
    </div>
  </div>
</template>

<script>
  import {
    getNowDate
  } from "network/api";
  export default {
    data() {
      return {
        district: null,
        nowtmp: null,
        // 引用本地图片，data中必须用require加载，否则会当成字符串来处理，后期后台传过来时需要修改
        imgurl: require("../../assets/img/mbe/qingtian@3x.png"),
      }
    },
    created() {
      getNowDate().then(res => {
        // console.log(res);
        const resData = res.data.HeWeather6[0];

        this.nowtmp = resData.now.tmp;
        this.district = resData.basic.location.toUpperCase();   // 大写
      })
    }
  }
</script>

<style>
  .main-board {
    height: 400px;
    box-shadow: 0 1px 1px rgba(100, 100, 100, .3);

    background: #9CECFB;
    /* fallback for old browsers */
    background: -webkit-linear-gradient(to left, #196aec, #65C7F7, #9CECFB);
    /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to left, #196aec, #65C7F7, #9CECFB);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

    display: flex;
    justify-content: center;

  }

  .weather-board {
    width: 200px;
    height: 300px;
    margin-top: 20px;
    display: flex;
    flex-direction: column;
  }

  .local {
    flex: 0.5;
    text-align: center;
  }

  .district {
    font-family: 'Futura-LT-Condensed-Medium', 'Futura-LT-Bold';
  }

  .nowtmp {
    flex: 1;
    text-align: center;
    font-weight: 700;
    font-size: 4em;

    position: relative;
  }

  .degree-symbol {
    position: absolute;
    top: 0;
    right: 36px;
    font-size: 60px;
    font-weight: 400;
  }


  .weather-big-icon {
    flex: 1;
    text-align: center;
  }

  .weather-big-icon img {
    height: 100px;
    vertical-align: top;
  }
</style>