<template>
  <div class="day7-forecast">
    <ul>
      <li v-for="(day, index) in days" :key="index">
        <span class="wdate">{{wdays[index]}} | {{days[index].date.substr(5, 5).replace("-", "/")}}</span>
        <span class="wtemp">{{days[index].tmp_min}}° - {{days[index].tmp_max}}°</span>
        <span class="wicon">
          <!-- <img :src="day7[1].wiconURL" /> -->
        </span>
      </li>
      <!-- 以下为假数据 应对一下 -->
      <li v-for="(day, index) in day7" :key="index">
        <span class="wdate">周一 | 07/06</span>
        <span class="wtemp">{{day7[index].minitemp}}° - {{day7[index].maxtemp}}°</span>
        <span class="wicon">
          <img :src="day7[index].wiconURL" />
        </span>
      </li>
    </ul>
  </div>
</template>

<script>
  import {
    getForecastDate
  } from "network/api";

  export default {
    data() {
      return {
        day7: {
          // 假数据
          d1: {
            wdate: "TODAY",
            minitemp: "29",
            maxtemp: "36",
            wiconURL: require("../../assets/img/mbe/wu@3x.png")
          },
          d2: {
            wdate: "TOMORROW",
            minitemp: "26",
            maxtemp: "31",
            wiconURL: require("../../assets/img/mbe/fuchen@3x.png")
          },
          d3: {
            wdate: "TUESDAY",
            minitemp: "28",
            maxtemp: "32",
            wiconURL: require("../../assets/img/mbe/duoyunzhuanqing@3x.png")
          },
          d4: {
            wdate: "WEDNESDAY",
            minitemp: "26",
            maxtemp: "30",
            wiconURL: require("../../assets/img/mbe/wu@3x.png")
          }
        },
        // now: [],
        days: [],
        wdays: ['今天', '明天']
      };
    },
    created() {
      getForecastDate().then(res => {

          // this.now = res.data.HeWeather6[0].now;
          this.days = res.data.HeWeather6[0].daily_forecast;

          var weekDay = ["周日", "周一", "周二", "周三", "周四", "周五", "周六"];
          for (var i = 2; i < this.days.length; i++) {
            this.wdays.push(weekDay[new Date(this.days[i].date).getDay()]); //2020-07-05
          }
        })
    }
  };
</script>

<style>
  .day7-forecast {
    width: 300px;
    height: 300px;

    margin: 0 auto;
    margin-top: -40px;
    margin-bottom: 20px;
    padding: 0 20px;
    background-color: #fff;
    box-sizing: border-box;
    border: 3px solid rgb(29, 29, 29);
    /* border-radius: 8px; */
    box-shadow: 3px 1px 12px rgba(100, 100, 100, 0.4);

    display: flex;
    flex-direction: column;
    /* align-items: center; */
    justify-content: center;
  }

  ul li {
    flex: 1;
    height: 30px;
    width: 100%;
    /* width: 250px; */
    margin: 8px 0;
    /* border: var(--bdBox); */
    display: flex;
    align-items: center;
  }

  ul li span {
    font-family: sans-serif;
    font-weight: 700;
    /* border: var(--bdBox); */
  }

  ul li .wdate {
    flex: 1;
  }

  ul li .wtemp {
    width: 76px;
  }

  ul li .wicon {
    width: 16px;
    height: 16px;
    /* overflow: hidden; */
    /* display: table-cell; */
    /* vertical-align: middle; */
  }

  ul li .wicon img {
    /* width: 16px; */
    /* height: 16px; */
    /* line-height: 16px; */
    width: 100%;
    height: auto;
    vertical-align: top;
    /* object-fit: 指定内容(img)适应到其使用的高度和宽度确定的框 */
    /* object-fit: scale-down; */
  }
</style>