import {request} from '@/network/request';

// now 实时天气数据
export function getNowDate() {
  return request({
    url: '/now',
    params: {
      location: 'auto_ip',
      key: '836a1476f2ec4e6895b2c4087e6c1bab',
      // lang: 'en'
    }
  })
}


